<template>
  <header class="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0 shadow">
  <a class="navbar-brand col-md-3 col-lg-2 me-0 px-3 fs-6" href="#">MountainBorrow</a>
  <button class="navbar-toggler position-absolute d-md-none collapsed"
  type="button" data-bs-toggle="collapse" data-bs-target="#sidebarMenu"
  aria-controls="sidebarMenu" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>
  <input class="form-control form-control-dark w-100 rounded-0 border-0"
  type="text" placeholder="Search" aria-label="Search">
  <div class="navbar-nav">
    <div class="nav-item text-nowrap">
      <a class="nav-link px-3" href="#" @click.prevent='logout()'>登出</a>
    </div>
  </div>
</header>

<div class="container-fluid">
  <div class="row">
    <nav id="sidebarMenu" style="height: 100vh"
    class="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse">
      <div class="position-sticky pt-3">
        <ul class="nav flex-column" >
          <li class="nav-item">
            <router-link to="/admin/products" class="nav-link align-text-bottom" >產品清單</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/admin/coupon" class="nav-link align-text-bottom" >優惠卷</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/admin/order" class="nav-link align-text-bottom" >訂單系統</router-link>
          </li>
           <li class="nav-item">
            <router-link to="/" class="nav-link align-text-bottom" >往前台</router-link>
          </li>
        </ul>
      </div>
    </nav>

    <main class="col-md-9 ms-sm-auto col-lg-10 px-md-4">
      <div class="d-flex justify-content-between flex-wrap flex-md-nowrap
      align-items-center pt-3 pb-2 mb-3 ">
        <!-- <h1 class="h2">Dashboard gogo</h1> -->
        <router-view v-if="checkSuccess"></router-view>
      </div>
    </main>
  </div>
</div>
</template>

